.App {
  text-align: center;
}

.menu-container{
  width: 100%;
  height: 3rem;
  background-color: black;
  color: beige;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: large;
  font-weight: bold;
}


#menu-home{
  display: flex;
  justify-content: center;
  margin-left: 10rem;
}

.menu-btn-container{
  display: flex;
  justify-content: flex-end;
}

.menu-btn{
  margin-right: 5rem;
}

.menu-btn:hover{
  color: red;
}

.modal-container{
    width: 30rem;
    height: 10rem;
    background-color: darkgoldenrod;
    color: purple;
}

.new-menu{
  width: 10em;
  height: 30rem;
  background-color: black;
  color: white;
}

.new-menu-title{
  width: 10em;
  height: 30rem;
  background-color: black;
  color: white;
}

.modal-btn{
  background-color: #61dafb;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}


.foot-container{
  width: 100%;
  height: 3rem;
  background-color: black;
  color: beige;
  align-content: center;
}



@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
